/*swipe*/
.slide{
    position: relative;
    max-width:100%;max-height:80%;
    overflow: hidden;
    margin: 0px auto;
}
.slide:after{
    content: '';
    display: block;
    width: 100%;
    padding-top: 50%;
}
.slide ul{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.slide li{
    list-style: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.slide li:first-child{
    z-index: 1;
}
.slide li img{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: none;
}
.slide .slide-desc {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    bottom: 0;overflow: hidden;
    padding: 5px;
    width: 100%;
    color: #fff;
    font-size: 16px;
    text-align: center;white-space: nowrap;word-break: break-all; text-overflow: ellipsis; }
.slide .dot{
    position: absolute;
    right: 10px;
    bottom: 10px;
    z-index: 5;
    font-size: 0;
}
#slide2 .dot{
    position: absolute;
    right:50%;
    bottom: 10px;
    z-index: 5;
    font-size: 0;text-align: center;
    margin:0 auto;
}
.slide .dot span{
    display: inline-block;
    width: 5px;
    height: 5px;
    margin-left: 5px;
    border: 1px solid #fff;
    border-radius: 50%;
}
.slide .dot .cur{
    background-color:#04BE02;border: 1px solid #04BE02;
}
.slide-m{
    background:#f8f8f8;height:40px;line-height:40px;vertical-align:middle;margin:0 auto;
}
.slide .txt{overflow: hidden;text-align:center;font-size:16px; white-space: nowrap;word-break: break-all; text-overflow: ellipsis;}
